import {reduxConstants} from '../ReduxConstants';

const initialState = {
  User: {},
  UserAddress: {
    addLine1: '',
  },
};

export const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case reduxConstants.USER:
      console.log('<<<<<<<<<<<UserReducer>>>>>>>>>>');
      console.log(action.User);
      console.log({User: action.User, ...state});
      console.log('<<<<<<<<<<<UserReducer>>>>>>>>>>');
      return {User: action.User, UserAddress: state.UserAddress};

    case reduxConstants.USER_ADDRESS:
      console.log('<<<<<<<<<<<UserReducer>>>>>>>>>>');
      console.log(action.User);
      console.log({User: state.User, UserAddress: action.UserAddress});
      console.log('<<<<<<<<<<<UserReducer>>>>>>>>>>');
      return {User: state.User, UserAddress: action.UserAddress};

    default:
      return state;
  }
};
